import styled from "styled-components";

export const MainCanvas = styled.canvas`
  background-color: blue;
  position: absolute;
  margin: ${(props) => {
    return props.dimensions.width > 1024
      ? `${props.dimensions.height * 0.05}px ${props.dimensions.width * 0.05}px`
      : ``;
  }};
  z-index: -1;
`;
